		if(type=="normal"){
			node={"originalLabels":["Process"],"originalProperties":{"cmd":"instance-"+i,"comm":"instance-"+i,"pid":{"low":111+i,"high":0},"fake":"true","title":"instance-"+i},"community":{"low":i,"high":0},"host":"instance-"+i,"label":"instance"+i,"x":x,"y":y+mult,"physics":false,"group":i,"id":"120920293"+i,"x":x,"y":y+mult,"shape":"circularImage","image":"https://zdnet4.cbsistatic.com/hub/i/r/2018/12/17/7e4a3f0e-5082-4516-8de2-260a72a2b81a/resize/370xauto/193ae1c14d785f69cbaf3541773d166e/cassandra-logo.gif"}
		}
